<template lang="jade">
a(href="javascript:;",
  class="weui_btn",
  :class="[typeClass, disabled ? 'weui_btn_disabled' : '', mini ? 'weui_btn_mini' : '']")
    slot
</template>

<script>
export default {
  props: {
    /**
     * 
     * primary: 
     * warn
     * default
     */
    type: {
      type: String,
      default: 'primary',
      required: false
    },

    /**
     * disabledtrue
     */
    disabled: {
      type: Boolean,
      default: false,
      required: false
    },

    /**
     * 
     */
    mini: {
      type: Boolean,
      default: false,
      required: false
    },

    /**
     * plainplain
     * primarydefaultplain
     */
    plain: {
      type: Boolean,
      default: false,
      required: false
    }
  },

  computed: {
    typeClass() {
      return `weui_btn${this.plain ? '_plain' : ''}_${this.type}`;
    }
  }
}
</script>



