<template>
<i :class="[nameClass, typeClass, class]"></i>
</template>

<script>
export default {
  props: {
    /**
     * 
     * success, success_circle, success_no_circle,
     * info, waiting, waiting_circle, circle,
     * warn, download, info_circle, cancel
     */
    name: {
      type: String,
      required: true
    },

    /**
     * 
     * default: 
     * message: 
     * safe: 
     */
    type: {
      type: String,
      required: false,
      default: 'default'
    },

    class: {
      type: String,
      required: false,
    }
  },

  computed: {
    nameClass() {
      return `weui_icon${this.type === 'safe' ? '_safe' : ''}_${this.name}`
    },

    typeClass() {
      switch(this.type) {
        case 'message':
          return 'weui_icon_msg';
        case 'safe':
          return 'weui_icon_safe';
        default:
          return false;
      }
    }
  }
}
</script>



