<template>
<div class="page dialog">
  <div class="hd">
    <div class="page_title">Dialog</div>
  </div>
  <div class="bd spacing">
    <weui-button type="primary" @click="showDialog(1)">Dialog</weui-button>
    <weui-button type="primary" @click="showDialog(2)">Dialog</weui-button>
  </div>
  <dialog v-show="dialog1Show" type="confirm" title=""
    confirm-button="" cancel-button=""
    @weui-dialog-confirm="handleDialogAction('', 1)"
    @weui-dialog-cancel="handleDialogAction('', 1)">
    
    <br>
    ...
  </dialog>
  <dialog v-show="dialog2Show" type="alert" title=""
    @weui-dialog-confirm="handleDialogAction('', 2)">
    
  </dialog>
</div>
</template>

<script>
import {Button, Dialog} from 'vue-wxui';

export default {
  data() {
    return {
      dialog1Show: false,
      dialog2Show: false
    }
  },

  methods: {
    showDialog(id) {
      this[`dialog${id}Show`] = true;
    },

    handleDialogAction(action, id) {
      if (action === '') {
        this[`dialog${id}Show`] = false;
      } else {
        alert('^^');
      }
    }
  },

  components: {
    'weui-button': Button,
    Dialog
  }
}
</script>



