<template>
<a class="weui_panel_ft" :href="link || 'javascript:;'" v-link="routerLink"><slot></slot></a>
</template>

<script>
export default {
  props: {
    /**
     * 
     */
    link: {
      type: String,
      required: false
    },

    /**
     * vue-router
     * vue-routerrouter-linklink
     */
    routerLink: {
      type: null,
      required: false
    }
  }
}
</script>



