<template>
<label :for="id" class="weui_cell weui_check_label">
  <cell-header>
    <input type="checkbox" :name="name" class="weui_check" :id="id" :value="value" v-model="checked">
    <span class="weui_icon_checked"></span>
  </cell-header>
  <cell-body>{{{label}}}</cell-body>
</label>
</template>

<script>
import CellHeader from './cell-header.vue';
import CellBody from './cell-body.vue';

export default {
  props: {
    /**
     * checkbox inputid
     */
    id: {
      type: String,
      required: false
    },

    /**
     * checkbox inputname
     */
    name: {
      type: String,
      required: false
    },

    /**
     * checkbox inputvalue
     */
    value: {
      type: String,
      required: false
    },

    /**
     * 
     */
    label: {
      type: String,
      required: true
    },

    /**
     * checkbox inputv-model
     */
    checked: {
      type: null,
      required: true,
      twoWay: true
    }
  },

  components: {
    CellHeader,
    CellBody
  }
}
</script>



