<template>
<div class="page actionSheet">
  <div class="hd">
    <div class="page_title">ActionSheet</div>
  </div>
  <div class="bd spacing">
    <weui-button type="primary" @click="showActionsheet">ActionSheet</weui-button>
  </div>
  <actionsheet :show.sync="actionsheetShow" :menus="actionSheetMenus" :actions="actionSheetActions"
    @weui-menu-click="handleMenuClick" @weui-action-click="handleActionClick"></actionsheet>
</div>
</template>

<script>
import {Actionsheet, Button} from 'vue-wxui';

export default {
  data() {
    return {
      actionsheetShow: false,
      actionSheetMenus: {
        menu1: '',
        menu2: '',
        menu3: '',
        menu4: ''
      },
      actionSheetActions: {
        action1: ''
      }
    }
  },

  methods: {
    showActionsheet() {
      this.actionsheetShow = true;
    },

    handleMenuClick(key) {
      alert(`${key}`);
    },

    handleActionClick(key) {
      alert('Actionsheet');
    }
  },

  components: {
    Actionsheet,
    'weui-button': Button
  }
}
</script>



