<template>
<div class="page cell">
  <div class="hd">
    <h1 class="page_title">Cell</h1>
  </div>
  <div class="bd">
    <cells-title></cells-title>
    <cells>
      <cell>
        <span slot="body"></span>
        <span slot="footer"></span>
      </cell>
    </cells>
    <cells-title></cells-title>
    <cells type="split">
      <cell>
        <img slot="header" :src="sampleImage" alt="" style="width:20px;margin-right:5px;display:block">
        <span slot="body"></span>
        <span slot="footer"></span>
      </cell>
      <cell>
        <img slot="header" :src="sampleImage" alt="" style="width:20px;margin-right:5px;display:block">
        <span slot="body"></span>
        <span slot="footer"></span>
      </cell>
    </cells>
    <cells-title></cells-title>
    <cells type="access">
      <link-cell link="javascript:;">
        <span slot="body">cell standard</span>
        <span slot="footer"></span>
      </link-cell>
      <link-cell link="javascript:;">
        <span slot="body">cell standard</span>
        <span slot="footer"></span>
      </link-cell>
    </cells>
    <cells-title></cells-title>
    <cells type="access">
      <link-cell link="javascript:;">
        <span slot="body">cell standard</span>
        <span slot="footer"></span>
      </link-cell>
      <link-cell link="javascript:;">
        <span slot="body">cell standard</span>
        <span slot="footer"></span>
      </link-cell>
    </cells>
    <cells-title></cells-title>
    <cells type="access">
      <link-cell link="javascript:;">
        <img slot="header" :src="sampleImage" alt="" style="width:20px;margin-right:5px;display:block">
        <span slot="body">cell standard</span>
        <span slot="footer"></span>
      </link-cell>
      <link-cell link="javascript:;">
        <img slot="header" :src="sampleImage" alt="" style="width:20px;margin-right:5px;display:block">
        <span slot="body">cell standard</span>
        <span slot="footer"></span>
      </link-cell>
    </cells>
    <template v-if="show">
      <cells-title></cells-title>
      <cells type="radio">
        <radio-cell id="x11" name="radio1" value="One" label="cell standard" :picked.sync="radioPicked">
        </radio-cell>
        <radio-cell id="x12" name="radio1" value="Two" label="cell standard" :picked.sync="radioPicked">
        </radio-cell>
      </cells>
      <cells-title></cells-title>
      <cells type="checkbox">
        <checkbox-cell id="s11" name="checkbox1" value="One" label="standard is dealt for u." :checked.sync="checkboxChecked"></checkbox-cell>
        <checkbox-cell id="s12" name="checkbox1" value="Two" label="standard is dealicient for u." :checked.sync="checkboxChecked"></checkbox-cell>
      </cells>
      <cells-title></cells-title>
      <cells type="form">
        <switch-cell name="switch" label="" :on.sync="switchOn"></switch-cell>
      </cells>
      <cells-title></cells-title>
      <cells type="form">
        <input-cell type="text" label="qq" placeholder="qq" :value.sync="qqValue"></input-cell>
        <input-cell type="text" label="" placeholder="" :vcode="vcode" :value.sync="vcodeValue"></input-cell>
        <input-cell type="number" label="" placeholder="" :value.sync="bankCardValue"></input-cell>
        <input-cell type="text" label="" placeholder="" :vcode="vcode" :warn="true" :value.sync="vcodeValue2"></input-cell>
      </cells>
      <cells-tips></cells-tips>
      <button-area>
        <weui-button type="primary"></weui-button>
      </button-area>
      <cells-title></cells-title>
      <cells type="form">
        <cell>
          <uploader slot="body" :count="3" :maxlength="5" @weui-input-change="handleFileInputChange">
            <span slot="title"></span>
            <uploader-files slot="uploader-files">
              <uploader-file image-url="http://shp.qpic.cn/weixinsrc_pic/pScBR7sbqjOBJomcuvVJ6iacVrbMJaoJZkFUIq4nzQZUIqzTKziam7ibg/"></uploader-file>
              <uploader-file image-url="http://shp.qpic.cn/weixinsrc_pic/pScBR7sbqjOBJomcuvVJ6iacVrbMJaoJZkFUIq4nzQZUIqzTKziam7ibg/"></uploader-file>
              <uploader-file image-url="http://shp.qpic.cn/weixinsrc_pic/pScBR7sbqjOBJomcuvVJ6iacVrbMJaoJZkFUIq4nzQZUIqzTKziam7ibg/"></uploader-file>
              <uploader-file image-url="http://shp.qpic.cn/weixinsrc_pic/pScBR7sbqjOBJomcuvVJ6iacVrbMJaoJZkFUIq4nzQZUIqzTKziam7ibg/" has-status>
                <icon slot="status" name="warn"></icon>
              </uploader-file>
              <uploader-file image-url="http://shp.qpic.cn/weixinsrc_pic/pScBR7sbqjOBJomcuvVJ6iacVrbMJaoJZkFUIq4nzQZUIqzTKziam7ibg/" has-status>
                <span slot="status">50%</span>
              </uploader-file>
            </uploader-files>
          </uploader>
        </cell>
      </cells>
      <cells-title></cells-title>
      <cells type="form">
        <input-cell type="textarea" placeholder="" :maxlength="200" :value.sync="commentsValue"></input-cell>
      </cells>
      <toptips v-show="toptipsCount > 0">&nbsp;{{toptipsCount}}</toptips>
      <cells-title></cells-title>
      <cells type="form">
        <input-cell type="number" label="" placeholder="" :value.sync="cardValue" :warn="true"></input-cell>
        <input-cell type="date" label="" :value.sync="dateValue"></input-cell>
        <input-cell type="datetime-local" label="" :value.sync="datetimeValue"></input-cell>
      </cells>
      <cells-title></cells-title>
      <cells type="split">
        <select-cell :before="true" :options="areaCodeOptions" :selected.sync="areaCodeSelected">
          <cell-input slot="body" type="text" placeholder="" :value.sync="phoneValue"></cell-input>
        </select-cell>
      </cells>
      <cells-title></cells-title>
      <cells type="split">
        <select-cell :options="contactTypeOptions" :selected.sync="contactTypeSelected"></select-cell>
        <select-cell :after="true" :options="nationOptions" :selected.sync="nationSelected">
          <span slot="header">/</span>
        </select-cell>
      </cells>
    </template>
  </div>
  <dialog v-show="alertShow" type="alert" title=""
    @weui-dialog-confirm="hideAlert">
    {{alertText}}
  </dialog>
</div>
</template>

<script type="text/ecmascript-6">
import {ButtonArea, Button,
  CellsTitle, CellsTips,
  Cells, Cell, LinkCell, RadioCell, CheckboxCell, SwitchCell, InputCell, SelectCell,
  CellInput,
  Uploader, UploaderFiles, UploaderFile,
  Toptips,
  Icon,
  Dialog} from 'vue-wxui';

const sampleImage = 'data:image/png;base64,';

import vcode from '../images/vcode.jpg';

export default {
  data() {
    return {
      show: false,
      radioPicked: 'One',
      sampleImage: sampleImage,
      checkboxChecked: ['Two'],
      switchOn: false,
      vcode: vcode,
      qqValue: '',
      vcodeValue: '',
      bankCardValue: '',
      vcodeValue2: '',
      commentsValue: '',
      toptipsCount: 2,
      cardValue: '',
      dateValue: '',
      datetimeValue: '',
      areaCodeOptions: ['+86', '+80', '+84', '+87'],
      areaCodeSelected: '+86',
      phoneValue: '',
      contactTypeOptions: [
        '',
        {
          value: 'qq',
          text: 'QQ'
        },
        'Email'],
      contactTypeSelected: '',
      nationOptions: ['', '', ''],
      nationSelected: '',
      alertText: '',
      alertShow: false
    }
  },

  methods: {
    handleFileInputChange(event) {
      const {files} = event.target;
      if (files.length > 0) {
        this.showAlert(`${files[0].name}`);
      }
    },

    showAlert(message) {
      this.alertText = message;
      this.alertShow = true;
    },

    hideAlert() {
      this.alertShow = false;
    }
  },

  components: {
    ButtonArea,
    'weui-button': Button,
    CellsTitle,
    CellsTips,
    Cells,
    Cell,
    LinkCell,
    RadioCell,
    CheckboxCell,
    SwitchCell,
    InputCell,
    SelectCell,
    CellInput,
    Uploader,
    UploaderFiles,
    UploaderFile,
    Toptips,
    Icon,
    Dialog
  },

  ready() {
    setTimeout(()=>{this.show = true;}, 200);
    const handler = setInterval(() => {
      if (this.toptipsCount === 1) {
        clearInterval(handler);
      }
      this.toptipsCount -= 1;
    }, 1000);
  }
}
</script>



