<template>
<div class="page toast">
  <div class="hd">
    <h1 class="page_title">Toast</h1>
  </div>
  <div class="bd spacing">
    <weui-button type="primary" @click="showToast">Toast</weui-button>
    <weui-button type="primary" @click="showLoadingToast">Loading Toast</weui-button>
  </div>
  <toast v-show="toastShow"></toast>
  <toast type="loading" v-show="loadingToastShow"></toast>
</div>
</template>

<script>
import {Button, Toast} from 'vue-wxui';

export default {
  data() {
    return {
      toastShow: false,
      loadingToastShow: false
    };
  },

  methods: {
    showToast() {
      this.toastShow = true;
      setTimeout(() => {
        this.toastShow = false;
      }, 2000);
    },
    showLoadingToast() {
      this.loadingToastShow = true;
      setTimeout(() => {
        this.loadingToastShow = false;
      }, 3000);
    },
  },

  components: {
    'weui-button': Button,
    Toast
  },
}
</script>



