<template>
<img :src="src" :alt="alt" class="weui_media_appmsg_thumb">
</template>

<script>
export default {
  props: {
    /**
     * src
     */
    src: {
      type: String,
      required: true
    },

    /**
     * alt
     */
    alt: {
      type: String,
      required: false
    }
  }
}
</script>



