<template>
<label :for="id" class="weui_cell weui_check_label">
  <cell-body>{{{label}}}</cell-body>
  <cell-footer>
    <input type="radio" :name="name" class="weui_check" :id="id" :value="value" v-model="picked">
    <span class="weui_icon_checked"></span>
  </cell-footer>
</label>
</template>

<script>
import CellBody from './cell-body.vue';
import CellFooter from './cell-footer.vue';
export default {
  props: {
    /**
     * radio inputid
     */
    id: {
      type: String,
      required: false
    },

    /**
     * radio inputname
     */
    name: {
      type: String,
      required: false
    },

    /**
     * radio inputvalue
     */
    value: {
      type: String,
      required: false
    },

    /**
     * 
     */
    label: {
      type: String,
      required: true
    },

    /**
     * radio inputv-model
     */
    picked: {
      type: String,
      required: true,
      twoWay: true
    }
  },

  components: {
    CellBody,
    CellFooter
  }
}
</script>



