<template>
<div class="page msg">
  <message icon="success" title="">
    <span slot="content"></span>
    <button-area slot="operation">
      <weui-button type="primary"></weui-button>
      <weui-button type="default"></weui-button>
    </button-area>
    <a href slot="extra"></a>
  </message>
</div>
</template>

<script>
import {Message, ButtonArea, Button} from 'vue-wxui';

export default {
  components: {
    Message,
    ButtonArea,
    'weui-button': Button
  }
}
</script>



