<template>
<div class="page button">
  <div class="hd">
    <h1 class="page_title">Button</h1>
  </div>
  <div class="bd spacing">
    <weui-button type="primary"></weui-button>
    <weui-button type="primary" :disabled="true"></weui-button>
    <weui-button type="warn"></weui-button>
    <weui-button type="warn" :disabled="true"></weui-button>
    <weui-button type="default"></weui-button>
    <weui-button type="default" :disabled="true"></weui-button>
    <div class="button_sp_area">
      <weui-button type="default" :plain="true"></weui-button>
      <weui-button type="primary" :plain="true"></weui-button>
      <weui-button type="primary" :mini="true"></weui-button>
      <weui-button type="default" :mini="true"></weui-button>
    </div>
  </div>
</div>
</template>

<script>
import {Button} from 'vue-wxui';

export default {
  components: {
    'weui-button': Button
  }
};
</script>



