<template>
<a :href="link" v-link="routerLink" class="weui_grid">
  <div class="weui_grid_icon" v-if="imageUrl">
    <img :src="imageUrl" alt="">
  </div>
  <p class="weui_grid_label" v-if="label">{{label}}</p>
</a>
</template>

<script>
export default {
  props: {
    /**
     * href
     */
    link: {
      type: String,
      required: false
    },

    /**
     * vue-router
     * vue-routerrouter-linklink
     * v-link
     */
    routerLink: {
      type: null,
      required: false
    },

    /**
     * 
     */
    imageUrl: {
      type: String,
      required: false
    },

    /**
     * 
     */
    label: {
      type: null,
      required: false
    }
  }
}
</script>



