<template>
<div class="weui_panel" :class="{'weui_panel_access': access}">
  <slot></slot>
</div>
</template>

<script>
export default {
  props: {
    /**
     * 
     */
    access: {
      type: Boolean,
      required: false,
      default: false
    }
  }
}
</script>



