<template>
<div class="page panel">
  <div class="hd">
    <h1 class="page_title">Panel</h1>
  </div>
  <div class="bd">
    <panel :access="true">
      <panel-header></panel-header>
      <panel-body>
        <media-box type="appmsg" link="javascript:;">
          <media-header>
            <h1>H1</h1>
          </media-header>
          <media-body>
            <media-title></media-title>
            <media-description></media-description>
          </media-body>
        </media-box>
        <media-box type="appmsg" link="javascript:;">
          <media-header>
            <media-appmsg-thumb src="data:image/png;base64,"></media-appmsg-thumb>
          </media-header>
          <media-body>
            <media-title></media-title>
            <media-description></media-description>
          </media-body>
        </media-box>
      </panel-body>
      <panel-footer link="javascript:;"></panel-footer>
    </panel>
    <panel access>
      <panel-header></panel-header>
      <panel-body>
        <media-box type="text">
          <media-body>
            <media-title></media-title>
            <media-description>
              
            </media-description>
          </media-body>
        </media-box>
        <media-box type="text">
          <media-body>
            <media-title></media-title>
            <media-description>
              
            </media-description>
          </media-body>
        </media-box>
      </panel-body>
      <panel-footer></panel-footer>
    </panel>
    <panel>
      <panel-header></panel-header>
      <panel-body>
        <media-box type="small_appmsg">
          <media-body>
            <cells type="access">
              <link-cell link="javascript:;">
                <img slot="header" src="data:image/png;base64," style="width:20px;margin-right:5px;display:block">
                <p slot="body"></p>
                <span slot="footer"></span>
              </link-cell>
              <link-cell link="javascript:;">
                <img slot="header" src="data:image/png;base64," style="width:20px;margin-right:5px;display:block">
                <p slot="body"></p>
                <span slot="footer"></span>
              </link-cell>
            </cells>
          </media-body>
        </media-box>
      </panel-body>
    </panel>
    <panel>
      <panel-header></panel-header>
      <panel-body>
        <media-box type="text">
          <media-body>
            <media-title></media-title>
            <media-description></media-description>
            <media-info>
              <media-info-meta></media-info-meta>
              <media-info-meta></media-info-meta>
              <media-info-meta extra></media-info-meta>
            </media-info>
          </media-body>
        </media-box>
      </panel-body>
    </panel>
  </div>
</div>
</template>

<script>
import {
  Panel, PanelHeader, PanelBody, PanelFooter,
  MediaBox, MediaHeader, MediaBody,
  MediaAppmsgThumb, MediaTitle, MediaDescription,
  MediaInfo, MediaInfoMeta,
  Cells, LinkCell
} from 'vue-wxui';

export default {
  components: {
    Panel, PanelHeader, PanelBody, PanelFooter,
    MediaBox, MediaHeader, MediaBody,
    MediaAppmsgThumb, MediaTitle, MediaDescription,
    MediaInfo, MediaInfoMeta,
    Cells, LinkCell
  }
}

</script>



