<template>
<div class="weui_cell weui_cell_switch">
  <cell-body>{{{label}}}</cell-body>
  <cell-footer>
    <input type="checkbox" :id="id" :name="name" class="weui_switch" v-model="on">
  </cell-footer>
</div>
</template>

<script>
import CellBody from './cell-body.vue';
import CellFooter from './cell-footer.vue';

export default {
  props: {
    /**
     * checkbox inputid
     */
    id: {
      type: String,
      required: false
    },

    /**
     * checkbox inputname
     */
    name: {
      type: String,
      required: false
    },

    /**
     * checkbox inputvalue
     */
    value: {
      type: String,
      required: false
    },

    /**
     * 
     */
    label: {
      type: String,
      required: true
    },

    /**
     * checkbox
     * checkbox inputv-model
     */
    on: {
      type: null,
      required: true,
      twoWay: true
    }
  },

  components: {
    CellBody,
    CellFooter
  }
}
</script>



