<template>
<a :href="link || 'javascript:;'" v-link="routerLink" class="weui_media_box" :class="typeClass" v-if="!!link || !!routerLink">
  <slot></slot>
</a>
<div class="weui_media_box" :class="typeClass" v-else>
  <slot></slot>
</div>
</template>

<script>
export default {
  props: {
    /**
     * 
     * appmsg
     * text
     * small_appmsg
     */
    type: {
      type: String,
      required: true
    },

    /**
     * 
     */
    link: {
      type: String,
      required: false
    },

    /**
     * vue-router
     * vue-routerrouter-linklink
     */
    routerLink: {
      type: null,
      required: false
    }
  },

  computed: {
    typeClass() {
      return `weui_media_${this.type}`;
    }
  }
}
</script>



