<template>
<div class="page search-bar">
  <div class="hd">
    <h1 class="page_title">Search Bar</h1>
  </div>
  <div class="bd">
    <search-bar placeholder="" cancel-text="" :value.sync="value"></search-bar>
    <cells type="access" class="search_show" v-show="!!value">
      <link-cell v-for="n in 4">
        <p slot="body">{{n + 1}}</p>
      </link-cell>
    </cells>
  </div>
</div>
</template>

<script>
import {SearchBar, Cells, LinkCell} from 'vue-wxui';

export default {
  data() {
    return {
      value: ''
    };
  },

  components: {
    SearchBar,
    Cells,
    LinkCell
  }
}
</script>



