<template>
<a :href="link || 'javascript:;'" v-link="routerLink" class="weui_tabbar_item" :class="{'weui_bar_item_on': isOn}">
  <div class="weui_tabbar_icon">
    <slot name="icon"></slot>
  </div>
  <div class="weui_tabbar_label">
    <slot name="label"></slot>
  </div>
</a>
</template>

<script>
export default {
  props: {
    /**
     * 
     */
    link: {
      type: String,
      required: false
    },

    /**
     * vue-router
     * vue-routerrouter-linklink
     */
    routerLink: {
      type: null,
      required: false
    },

    /**
     * 
     */
    isOn: {
      type: Boolean,
      required: false,
      default: false
    }
  }
}
</script>



